import { OpaqueToken } from "@angular/core";
import { environment } from '../environments/environment';

export let APP_CONFIG = new OpaqueToken("app.config");

export interface IAppConfig {
    version: string;
    servicesBaseUrl: string;
}

//       Debug
export var DevServicesBaseUrl = "https://www.cms.gr/PublicApi";
if (!environment.production) {
    //   debuger  VS2013
    DevServicesBaseUrl = "http://localhost:34488";
    //   ptf-pc IIS
    //DevServicesBaseUrl = "http://www.cms.gr/PublicApi";
}

export const DevAppConfig: IAppConfig = {    
    version: "v 1.0",
    servicesBaseUrl : DevServicesBaseUrl
};


